import React from 'react';
import SoftwareSkill from '../../components/softwareSkills/SoftwareSkill.jsx';
import { skillsSection } from '../../portfolio';
import { Fade } from 'react-reveal';

import Skills_illustration from '../../assets/images/developerActivity.svg';

export default function Skills() {
  return (
    <div className="main" id="skills">
      <div className="skills-main-div">
        <Fade left duration={1000}>
          <div className="skills-image-div">
            <img alt="Working" src={Skills_illustration}></img>
          </div>
        </Fade>
        <Fade right duration={1000}>
          <div className="skills-text-div">
            <h1 className="skills-heading">{skillsSection.title} </h1>
            <p className="subTitle skills-text-subtitle">
              {skillsSection.subTitle}
            </p>
            <SoftwareSkill />
            <div>
              {skillsSection.skills.map((skills, idx) => {
                return (
                  <p key={idx} className="subTitle skills-text">
                    {skills}
                  </p>
                );
              })}
            </div>
          </div>
        </Fade>
      </div>
    </div>
  );
}
